#enp-quiz {
    .enp-quiz-breadcrumbs {
        grid-row: 1;
        // @include flex-center;
        // position: absolute;
        // top: 0;
        // width: 1000%;
        // left: -500%;
        // right: -500%;
        // margin-left: auto;
        // margin-right: auto;
        // background: #fff;
        // border-bottom: 1px solid #ddd;
        // padding: 10px;
        // z-index: 99999;
    }

    .enp-quiz-breadcrumbs__list {
        @include flex-inline;
    }

    .enp-quiz-breadcrumbs__item {
        @include flex-inline__item ;
        @include flex-center;
        fill: $link;
        margin-right: 5px;

        @include breakpoint(small) {
            margin-right: 10px;
        }
    }

    .enp-quiz-breadcrumbs__link--disabled {
        @include disabled;
    }

    .enp-quiz-breadcrumbs__link--active {
        @include btn--ghost;
        @include btn--thin;
    }

    .enp-quiz-breadcrumbs__link {
        font-size: 1rem;
        font-weight: 400;
        text-transform: none;
        letter-spacing: 0;
    }

    // JS
    .enp-quiz-breadcrumbs--fixed {
        top: 0;
        position: fixed;
        animation: navSlideIn .25s;
    }
}
