@keyframes slideInBottom {
  0% {
    opacity: 0;
    transform: translate3d(0, 100px, 0);
  }
  100% {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
}
@keyframes slideInTop {
  0% {
    opacity: 0;
    height: auto;
    transform: translate3d(0, -20px, 0);
  }
  100% {
    opacity: 1;
    height: auto;
    transform: translate3d(0, 0, 0);
  }
}
