  border-radius: 50%;
  border: 1px solid #aaa;
  position: absolute;
  z-index: 2;
  width: 1.2rem;
  height: 1.2rem;
  top: -0.4rem;
  margin-left: -0.6rem;
  cursor: default;
  -ms-touch-action: none;
  touch-action: none;
  transition: opacity 0.2s cubic-bezier(0, 0, 0.3, 1), height 0.18s cubic-bezier(0, 0, 0.3, 1), width 0.18s cubic-bezier(0, 0, 0.3, 1), transform 0.18s cubic-bezier(0, 0, 0.3, 1);
}
#enp-quiz .enp-slider .ui-slider-handle:focus {
  outline: none;
  box-shadow: 0 0 3px 1px #4D90FE;
}
#enp-quiz .enp-slider .ui-slider-handle--correct,
#enp-quiz .enp-slider .ui-slider-handle--incorrect {
  width: 1.1rem;
  height: 1.1rem;
  top: -0.4rem;
  margin-left: -0.55rem;
}
#enp-quiz .enp-slider .ui-slider-handle--incorrect {
  z-index: 1;
  border: 2px solid #bf5700;
}
#enp-quiz .enp-slider .ui-slider-handle--correct {
  border: 2px solid #60AAAD;
  z-index: 99999;
}
#enp-quiz .enp-slider .ui-slider-range {
  position: absolute;
  z-index: 1;
  font-size: 0.7rem;
  display: block;
  border: 0;
  background-position: 0 0;
  top: 0;
  height: 100%;
}
#enp-quiz .enp-slider .ui-slider-range-show-correct {
  background: #60AAAD;
  z-index: 9;
}
#enp-quiz .enp-slider .ui-slider-range-min {
  left: 0;
  background: #00A9B7;
}
#enp-quiz .enp-slider .ui-slider-range-max {
  right: 0;
}
#enp-quiz .enp-slider {
  /* support: IE8 - See #6727 */
}
#enp-quiz .enp-slider.ui-state-disabled .ui-slider-handle, #enp-quiz .enp-slider.ui-state-disabled .ui-slider-range {
  filter: inherit;
}
#enp-quiz .ui-slider-horizontal {
  height: 0.3rem;
}
#enp-quiz .enp-question__answered .enp-slider {
  background: #bbb;
}
#enp-quiz .enp-question__answered .ui-slider-range-min {
  background: #fff;
}